@use '../../assets/scss/mixins';
@use '../../assets/scss/variables';

.Page {
  @include mixins.flex-box();
  width: 100%;
  margin-top: 50px;

  &__content {
    @include mixins.box-style();
    @include mixins.flex-box();
    padding: 20px;
  }

  & button {
    margin: 0 10px;
  }
}